/* @font-face {
  font-family: 'Spoqa Han Sans Neo', 'sans-serif';
  src: url(./fonts/SpoqaHanSansNeo-Regular.ttf) format('truetype');
} */
@import url(https://spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css);

* {margin: 0; padding: 0;}
body {font-family: 'Spoqa Han Sans Neo', 'sans-serif';
background-color: #fff;}
li {list-style: none;}
a {text-decoration: none; color: #000;}
img {display: block;}

.header {
  width: 100%; /* height: 50px; */
  position: relative;
}
.header_top {
  width: 100%; height: 50px;
}
.header_top .header_logo {
  width: 25%;
  max-height: 35px;
  float: left;
}
.header_top .header_logo img {
  max-height: 35px;
  margin: 10px 0 0 12px;
}
.header_top .mobile_gnb {
  float: right;
  overflow: hidden;
}
.header_top .mobile_gnb>li{
  float: left;
  margin-right: 12px;
  font-size: 1.1rem;
  line-height: 50px;
}
.header_top .mobile_gnb .btn_menu {
  width: 1.1rem;
  display: inline
}
.header_top .mobile_gnb .btn_menu .btn_hammenu {
  position: relative;
  width: 18px; height: 13px;
  border: none;
  background-color: #fff;
  cursor: pointer;
}
.header_top .mobile_gnb .btn_menu .btn_hammenu span {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  background-color: #000;
  border: 1px solid #000;
  transition: all 0.3s;
}
.header_top .mobile_gnb .btn_menu .btn_hammenu .ham_top {
  top: -7px;
}
.header_top .mobile_gnb .btn_menu .btn_hammenu .ham_middle {
  top: -1px;
}
.header_top .mobile_gnb .btn_menu .btn_hammenu .ham_bottom {
  bottom: 5px;
  background-color: #000;
}
.header_top .mobile_gnb .btn_menu .btn_hammenu.active .ham_top {
  transform: translateY(6px) rotate(-45deg);
}
.header_top .mobile_gnb .btn_menu .btn_hammenu.active .ham_middle {
  opacity: 0;
}
.header_top .mobile_gnb .btn_menu .btn_hammenu.active .ham_bottom {
  transform: translateY(-6px) rotate(45deg);
}


.header_top .pc_gnb {
  float: right;
  display: none;
}
.header_top .pc_gnb li {
  font-size: 0.625rem;
  display: inline-block;
  line-height: 50px;
  margin-right: 20px;
  transition: all 0.2s;
}
.header_top .pc_gnb li:hover {
  font-weight: bold;
}

.lnb_box {
  width: 100%; height: 40px;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  display: none;
}
.lnb {
  width: 100%; height: 40px;
  margin: 0 auto;
  font-size: 0.825rem;
  position: relative;
}
.lnb .lnb_menu {
  width: 14.285714%;
  height: 40px;
  float: left;
}

.lnb .lnb_menu>a {
  display: block;
  line-height: 40px;
  margin-left: 20px;
  transition: all 0.2s;
}
.lnb .lnb_menu>a:hover {
  font-weight: bold;
}
.lnb .lnb_menu ul {
  width: 14.285714%;
  height: 245px;
  border-top: 1px solid #ccc;
  position: absolute;
  top: 40px;
  z-index: 3333;
  background-color: #fff;
  display: none;
}
.lnb .lnb_menu ul li {
  width: 100%; height: 35px;
  padding: 8px 0 8px 15px;
  box-sizing: border-box;
  transition: all 0.2s;
}
.lnb .lnb_menu ul li:hover {
  background-color: #eee;
  font-weight: bold;
}
.lnb .lnb_menu ul li a {
  display: block;
  font-size: 0.75rem;
  color: #444;
}
.lnb:hover .lnb_menu ul {display: block;}
    /* 풀다운 */


/* ---------- ham_menu ---------- */
.ham_menu {
  width: 100%; height: 100vh;
  font-size: 14px;
  position: absolute;
  background-color: #fff;
  z-index: 22222;
  left :-1000px;
  transition: all 0.5s
}
.menu_left {
  left: 0;
}
.ham_menu>ul {
  width: 100%; height: 40px;
  line-height: 40px;
  border-top: 1px solid #ccc;
}
.ham_menu>ul:last-child {
  border-bottom: 1px solid #ccc;
}
.ham_menu .ham_menu>ul>li i {
  font-size: 1.3rem;
}
.ham_menu .ham_menu01 li {
  width: 25%;
  float: left;
  text-align: center;
  border-right: 1px solid #ccc;
  box-sizing: border-box;
}
.ham_menu .ham_menu01 li:last-child {
  border-right: none;
}
.ham_menu .ham_menu01 li a {
  display: block;
}
.ham_menu .ham_menu02 li {
  width: 50%;
  float: left;
  text-align: center;
  border-right: 1px solid #ccc;
  box-sizing: border-box;
}
.ham_menu .ham_menu02 li:last-child {
  border-right: none;
}
.ham_menu .ham_menu02 li a {
  margin-right: 20px;
}
.ham_menu .ham_menu02 li a:last-child {
  margin-right: none;
}

.ham_menu .ham_list {
  width: 100%;
}
.ham_menu .ham_list_wrap {
  width: 100%;
}
.ham_menu .ham_list_wrap .ham_list .collapsible {
  width: 100%; height: 40px;
  border: none;
  background-color: #fff;
  border-top: 1px solid #ccc;
}

.ham_menu .ham_list_wrap .ham_list .collapsible span:first-child {
  float: left;
  margin-left: 18px;
  font-weight: bold;
}
.ham_menu .ham_list_wrap .ham_list .collapsible span:nth-child(2) {
  float: right;
  margin-right: 18px;
}
.ham_menu .ham_list_wrap .ham_list .ham_menu_li {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}
.ham_menu .ham_list_wrap .ham_list .ham_menu_li li {
  width: 100%; height: 40px;
  border-top: 1px solid #ccc;
  background-color: #eee;
}
.ham_menu .ham_list_wrap .ham_list .ham_menu_li li a {
  display: block;
  margin-left: 30px;
  line-height: 40px;
}

/* ---------- breadcrumbs ---------- */
#btn_breadcrumbs {display: none;}
#btn_breadcrumbs:checked ~ .breadcrumbs_menu {display: block;}
#btn_breadcrumbs:checked ~ .latelymenu i {transform:rotate(180deg)}

.breadcrumbs {
  width: 100%; height: 30px;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  position: relative;
}
.breadcrumbs a {
  font-size: 0.75rem;
  margin-left: 10px;
  line-height: 30px;
  text-align: center;
  transition: all 0.2s;
}
.breadcrumbs label {
  font-size: 0.75rem;
  margin-left: 10px;
  line-height: 30px;
  cursor: pointer;
  transition: all 0.2s;
}
.breadcrumbs label i {
  transition: all 0.2s;
}
.breadcrumbs .breadcrumbs_menu {
  font-size: 0.75rem;
  position: absolute;
  z-index: 44444;
  top: 30px; left: 14%;
  display: none;
}
.breadcrumbs .breadcrumbs_menu li a {
  display: block;
  padding: 4px;
  background-color: #fff;
  box-sizing: border-box;
}
/* ---------- visual_main ---------- */
.visual_main {
  width: 100%; height: 200vh;
  position: relative;
  overflow: hidden;
}
.visual_main_title {
  width: 100%; height: 100vh;
  position: absolute;
  top: 0%; left: 0%;
  color: #fff;
  font-size: 0.875rem;
}
.visual_main_title p {
  text-align: center;
  color: #fff;
  font-size: 0.75rem;
  margin-top: 20px;
}
.visual_main_title .vm_main_title {
  font-weight: bold;
  font-size: 1.25rem;
}

.visual_main_txt {
  width: 100%; height: 100vh;
  position: absolute;
  top: 50%; left: 0%;
  background: linear-gradient(transparent, #fff , #fff );
}
.visual_main_txt .vm_txt_bold {
  width: 100%;
  font-weight: bold;
  text-align: center;
}
.visual_main_txt hr {
  width: 70%;
  margin: 20px auto;
  border: 1px solid #000;
}
.visual_main_txt .vm_txt {
  width: 90%;
  font-size: 0.75rem;
  text-align: center;
  margin: 0 auto;
}

/* ---------- history ---------- */
#btn_year_move {display: none;}
#btn_year_move:checked ~ .year_move_view {
  border-radius: 0;
  background-color: #ccc;
  opacity: 1;
}

#btn_year_move:checked ~ .year_move_menu {left: 40px}
.year_move {
  width: 100%;
  overflow: hidden;
  display: flex;
}
.year_move a {
  flex-grow: 1;
  display: block;
  float: left;
  text-align: center;
  font-size: 0.875rem;
  padding: 2% 0;
  background-color: #eee;
  border-right: 1px solid #fff;
}
.year_move_wrap {
  position: fixed;
  bottom: 100px; left: 0;
  z-index: 44444;
  display: none;
}
.year_move_tablet_pc {
  position: relative;
}
.year_move_tablet_pc .year_move_view {
  font-size: 0.75rem;
  width: 20px; height: 77px;
  background-color: #eee;
  padding: 20px 10px;
  border-radius: 0 10px 10px 0;
  cursor: pointer;
  transition: all 0.3s;
  position: absolute;
  bottom: 5px; left: 0px;
}
.year_move_tablet_pc .year_move_menu {
  cursor: pointer;
  transition: all 0.3s;
  position: absolute;
  bottom: 5px; left: -120px;
}
.year_move_tablet_pc .year_move_menu div {
  width: 110px; height: 35px;
  font-size: 0.75rem;
  background-color: #eee;
  text-align: center;
  line-height: 35px;
  padding: 2px 0;
}
.year_move_tablet_pc .year_move_menu div a {
  display: block;
}
.year_move_tablet_pc .year_move_menu div:first-child {
  border-radius: 0 10px 0 0;
}
.year_move_tablet_pc .year_move_menu div:last-child {
  border-radius: 0 0 10px 0;
}



.tab_fixed {
  position: fixed;
  top: 0; left: 0;
  z-index: 33333;
}
.history_title {
  font-weight: bold;
  width: 100%;
  text-align: center;
  background-color: #fff;
}
.history_wrap {
  width: 100%;
  background-color: #fff;
  position: relative;
}

.history {
  width: 100%;
  background-color: #fff;
  overflow: hidden;
}
.history .line {
  width: 50%; height: 100%;
  border-right: 1px solid #ccc;
  position: absolute;
  display: none;
}
.slate01 {
  background-image: url(./images/slate_number_fontsize_8_28x28/slate_01.svg);
}
.slate02 {
  background-image: url(./images/slate_number_fontsize_8_28x28/slate_02.svg);
}
.slate03 {
  background-image: url(./images/slate_number_fontsize_8_28x28/slate_03.svg);
}
.slate04 {
  background-image: url(./images/slate_number_fontsize_8_28x28/slate_04.svg);
}
.slate05 {
  background-image: url(./images/slate_number_fontsize_8_28x28/slate_05.svg);
}
.slate06 {
  background-image: url(./images/slate_number_fontsize_8_28x28/slate_06.svg);
}
.slate07 {
  background-image: url(./images/slate_number_fontsize_8_28x28/slate_07.svg);
}
.slate08 {
  background-image: url(./images/slate_number_fontsize_8_28x28/slate_08.svg);
}
.slate09 {
  background-image: url(./images/slate_number_fontsize_8_28x28/slate_09.svg);
}
.slate10 {
  background-image: url(./images/slate_number_fontsize_8_28x28/slate_10.svg);
}
.slate11 {
  background-image: url(./images/slate_number_fontsize_8_28x28/slate_11.svg);
}
.slate12 {
  background-image: url(./images/slate_number_fontsize_8_28x28/slate_12.svg);
}

.history_contents {
  overflow: hidden;

}
.history_img {
  display: none;
}
.history_img img{
  width: 100%;
}
.history_contents .history_box {
  margin: 10% auto;
  opacity: 0;
  transition: all 1s;

  
}
.history_contents .history_box .year {
  font-weight: bold;
  font-size: 1.125rem;
  text-align: center;
  margin-bottom: 3%;
}
.history_contents .history_box .content {
  width: 100%;
  font-size: 0.75rem;
  border-top: 1px dashed #ccc;
  border-bottom: 1px solid #ccc;
  padding: 5%;
  box-sizing: border-box;
}
.history_contents .history_box .img_2021 {
  background-image: url(./images/history01_600x340_op20_mobile.jpg);
}
.history_contents .history_box .img_2018 {
  background-image: url(./images//history02_600x412_op20_mobile.jpg);
}
.history_contents .history_box .img_2012 {
  background-image: url(./images/history03_600x309_op20_mobile.jpg);
}

.history_contents .history_box .content .slate {
  width: 28px; height: 28px;
  float: left;
  margin-right: 15px;
}
.history_contents .history_box .content li {
  width: 100%;
  padding: 5px 0;
}
.history_contents .history_box .content li ul {
  overflow: hidden;
}
.history_contents .history_box .content li ul li {
  float: left;
}
.history_contents .history_box .content li:last-child {
  margin-bottom: 0;
}
/* ---------- footer ---------- */
.footer {margin-top: 100px;}
.right {
  float: right;
  margin-right: 2%;
}
.left {
  float: left;
  margin-left: 2%;
}
.footer_top .footer_content {
  width: 100%; height: 40px;
  border-bottom: 1px solid #ccc;
  font-size: 0.825rem;
}
.footer_top .footer_content .footer_title {
  line-height: 40px;
}
.footer_top .newsletter {
  border-top: 1px solid #ccc;
}
.footer_top .newsletter a {
  padding: 5px 20px;
  background-color: #2e3192;
  border-radius: 20px;
  color: #fff;
  margin-top: 6px;
}

#chk_footer_menu {display: none;}
#chk_footer_menu:checked ~ .footer_menu_open {display: block;}

.footer_top .footer_menu {
  background-color: #eee;
  position: relative;
}
.footer_top .footer_menu label i {
  float: right;
  margin-right: 5%;
  line-height: 40px;
}
.footer_top .footer_menu .footer_menu_open {
  position: absolute;
  top: 40px; left: 0;
  width: 100%; height: 130px;
  background-color: #fff;
  border: 1px solid #ccc;
  overflow: hidden;
  display: none;
  z-index: 2222;
}
.footer_top .footer_menu .footer_menu_open ul{
  float: left;
  margin-left: 16%;
}
.footer_top .footer_menu .footer_menu_open li{
  margin: 10px 0;
}

.footer_top .footer_menu_pc {
  display: none;
  overflow: hidden;
}
.footer_top .footer_menu_pc ul{
  float: left;
  line-height: 20px;
  border-right: 1px solid #ccc;
  padding: 0 8%;
  box-sizing: border-box;
  font-size: 0.75rem;
  order: 1;
}

.footer_top .organization .organization_slide,
.footer_top .mecenat .mecenat_slide {
  width: 50%;
  overflow: hidden;
}
.footer_top .organization .organization_slide .organization_slide_wrap {
  display: flex;
  width: 1050%;
  animation-name: organization_slide;
  animation-iteration-count: infinite;
  animation-duration: 40s;
}
@keyframes organization_slide {
  0% {transform: translateX(0);}
  10% {transform: translateX(-9.090908%);}
  20% {transform: translateX(-18.181816%);}
  30% {transform: translateX(-27.272724%);}
  40% {transform: translateX(-36.363632%);}
  50% {transform: translateX(-45.45454%);}
  60% {transform: translateX(-54.545448%);}
  70% {transform: translateX(-63.636356%);}
  80% {transform: translateX(-72.727264%);}
  90% {transform: translateX(-81.818172%);}
  100% {transform: translateX(-90.90908%);}
}
.footer_top .organization .organization_slide .organization_slide_wrap a {
  width: 4.8%;
  max-height: 30px;
  margin-right: 5px;
  margin-top: 8px;
}
.footer_top .organization .organization_slide .organization_slide_wrap img,
.footer_top .mecenat .mecenat_slide .mecenat_slide_wrap img {
  width: 100%;
  max-width: 100.625px;
  max-height: 30px;
} 
.footer_top .mecenat .mecenat_slide .mecenat_slide_wrap {
  display: flex;
  width: 300%;
  animation-name: mecenat_slide;
  animation-iteration-count: infinite;
  animation-duration: 8s;
}
@keyframes mecenat_slide {
  0% {transform: translateX(0);}
  50% {transform: translateX(-33.333332%);}
  100% {transform: translateX(-66.666664%);}
}
.footer_top .mecenat .mecenat_slide .mecenat_slide_wrap a {
  width: 16.666666%;
  max-height: 30px;
  margin-right: 5px;
  margin-top: 8px;
}

.footer_bottom {
  background-color: #eee;
  padding: 5%;
}
.footer_bottom ul {
  margin: 20px 0;
}
.footer_bottom ul li {
  font-size: 0.75rem;
  color: #666;
  margin-bottom: 5px;
  text-align: center;
}
.footer_bottom ul li a{
  font-size: 0.625rem;
  border-radius: 20px;
  margin-left: 5px;
  background-color: #999;
  color: #fff;
  font-weight: 200;
  padding: 2px;
}
.footer_bottom .footer_logos {
  height: 45px;
  display: flex;
}
.footer_bottom .footer_logos div {
  width: 125px;
}
.footer_bottom .footer_logos div:nth-child(2) {
  margin-left: 20px;
}
.footer_bottom .footer_logos div img {
  width: 100%;
}

/* ---------- quick ---------- */
.quick {
  position: fixed;
  bottom: 5%; right: 2%
}
.quick a {
  display: block;
  width: 40px; height: 40px;
  border-radius: 20px;
  text-align: center;
  margin-bottom: 3px;
  box-sizing: border-box;
}
.quick .quick_reserve {
  font-size: 0.625rem;
  background-color: #2e3192;
  color: #fff;
  padding-top: 5px;
  animation-name: quick_color;
  animation-duration: 12s;
  animation-iteration-count: infinite;
}
@keyframes quick_color {
  0% {background-color: #2e3192;}
  25% {background-color: #ee1997;}
  50% {background-color: #8dc854;}
  75% {background-color: #1ab6f1;}
  100% {background-color: #2e3192;}
  
}
.quick .btn_arrow {
  font-size: 1.2rem;
  background-color: #fff;
  line-height: 40px;
  border: 1px solid #888;
}

/* ---------- media ---------- */
@media all and (max-width:600px){
}

@media all and (min-width: 601px) {


  .year_move {display: none;}
  .history .line {display: block;}
  .history_wrap .year_move_wrap {display: block;}
  .history_box {
    display:block;
    width: 49%;}
  .history_left {
    float:left;}
  .history_right {
    float:right;
    padding-top: 150px;}

  .history_img {
    display: block;
    width: 45%;
    padding-left: 2%;
  }

  .history_contents .history_box .img_2021, 
  .history_contents .history_box .img_2018, 
  .history_contents .history_box .img_2012 {
    background-image: none;
  } 

  .footer_bottom {text-align: center;}
  .footer_bottom .footer_logos {justify-content: center;}
  .footer_bottom .footer_logos div {width: 160px;}
  .footer_top .organization .organization_slide,.footer_top .mecenat .mecenat_slide {width: 60%;}
  .footer_top .organization .organization_slide .organization_slide_wrap {width: 700%;}
  .footer_top .organization .organization_slide .organization_slide_wrap a {width: 7%;}
}


@media all and (min-width: 769px) {
  .header .mobile_gnb {display: none;}
  .header .pc_gnb {display: block;}

  .header .lnb_box {display: block;}
  .header .lnb {display: block;}

  .ham_menu {display: none;}


  .breadcrumbs {display: none;}
  .visual_main_txt .vm_txt {
    width: 50%;
  }

  .footer .footer_top {
    display: flex;
    margin-bottom: 20px;
  }
  .footer_top .newsletter {border-top: none;}
  .footer .footer_top .footer_content{
    border-bottom: none;
    height: 100%;
  }
  .footer .footer_top .organization,
  .footer .footer_top .mecenat {
    height: 50%;
  }
  .footer_top .footer_menu {display: none;}

  .footer_top .footer_menu_pc {display: block;
  width: 30%;}
  .footer_top .footer_wrap {
    width: 45%;
    order: 2;
    border-right: 1px solid #ccc;
  }
  .footer_top .newsletter {
    width: 25%;
    order: 3;
  }
  
}

@media all and (min-width: 1024px) {
  .footer_top .organization .organization_slide,.footer_top .mecenat .mecenat_slide {width: 70%;}

  .footer_top .footer_menu_pc {
    width: 30%;}
    .footer_top .footer_wrap {
      width: 55%;
      order: 2;
      border-right: 1px solid #ccc;
    }
    .footer_top .newsletter {
      width: 35%;
      order: 3;
    }
}

@media all and (min-width: 1361px) {
  .header .header_top, .header .lnb, .year_move, .history_title, .history {
    width: 1360px;
    margin-left: auto;
    margin-right: auto;
  }
  .history .line {
    max-width: 680px;
  }
  .footer {
    width: 1360px;
    margin: 10% auto 0;
  }
  
}